





body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  background-image:
    linear-gradient(
      to bottom, 
      #7986cb,
      #7986cb 280px,
      #f5f5f5 50px,
      #f3f3f3 90%,
      #7986cb 0%
    );

    
}

body {
  display: flex;
  min-height: 100vh;
  flex-direction: column;
}

main {
  flex: 1 0 auto;
}
.banner{
  background-image: url("./assets/banner_updated.png");
  object-fit:fill;

